@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@300;400;600;700&display=swap);
.header-container {
    position: relative;
    height: 60px;
    width: auto;
    display: flex;
    box-shadow: 0 1px 5px -1.9px rgb(0, 0, 0);
}

.header-container>.logo {
    position: relative;
    bottom: 11px;
}

.header-container>.logo img {
    height: 80px;
    width: 80px;
}

.header-container>.logo-title>h1 {
    position: relative;
    top: 14px;
    padding: 3px 22px;
    font-size: var(--regular);
    color: var(--orange);
}

.header-container>.logo-title>h1::after {
    content: '.me';
    font-size: var(--regular);
    color: var(--primary-red);
    -webkit-animation: colorchange 20s infinite alternate;
            animation: colorchange 20s infinite alternate;
}

@-webkit-keyframes colorchange {
    0% {
        color: var(--primary-red);
    }
    10% {
        color: var(--violet);
    }
    20% {
        color: var(--blue);
    }
    30% {
        color: var(--orange);
    }
    40% {
        color: var(--primary-red);
    }
    50% {
        color: var(--primary-red);
    }
    60% {
        color: var(--violet);
    }
    70% {
        color: var(--blue);
    }
    80% {
        color: var(--orange);
    }
    90% {
        color: var(--orange);
    }
    100% {
        color: var(--primary-red);
    }
}

@keyframes colorchange {
    0% {
        color: var(--primary-red);
    }
    10% {
        color: var(--violet);
    }
    20% {
        color: var(--blue);
    }
    30% {
        color: var(--orange);
    }
    40% {
        color: var(--primary-red);
    }
    50% {
        color: var(--primary-red);
    }
    60% {
        color: var(--violet);
    }
    70% {
        color: var(--blue);
    }
    80% {
        color: var(--orange);
    }
    90% {
        color: var(--orange);
    }
    100% {
        color: var(--primary-red);
    }
}
.wrapper {
    width: 100%;
    position: relative;
}

.container {
    position: relative;
    display: flex;
    width: 100%;
    padding: 0 125px;
    margin: 70px 0 0 0;
    justify-content: space-around;
    align-items: flex-start;
}

.main-heading {
    position: relative;
    top: 28%;
    left: 23px;
}
.main-containt {
    opacity: 0;
    transition: opacity 0.6s;
}

.welcome_text h1 {
    font-size: var(--medium);
    color: var(--orange);
}

.name_text h1 {
    font-size: var(--large);
    color: var(--violet);
}

.tagline {
    position: relative;
}

.tagline h1 {
    font-size: var(--small);
    color: var(--orange);
    text-align: left;
}
.about-me {
    opacity: 0;
    transition: opacity 0.6s;
}

.about_containt {
    width: 360px;
    margin: 26px 0px;
}

.about_containt p {
    font-size: var(--small);
    color: var(--primary-red);
    text-align: justify;
}
.skills {
    opacity: 0;
    transition: opacity 0.6s;
}

.skills h1 {
    font-size: var(--regular);
    color: var(--blue);
    text-align: left;
}

.skills>.tech_logo {
    display: flex;
    align-content: center;
    justify-content: flex-start;
    position: relative;
    top: 26px;
}

.skills>.tech_logo {
    display: flex;
    align-content: center;
    justify-content: flex-start;
    position: relative;
    top: 26px;
}

.tech_logo img {
    width: 28px;
    height: 28px;
    margin: 0 3px;
}
.avater_img img {
    width: 388px;
    height: 388px;
}

.fade-in-image {
    animation: fadeIn 5s;
    -webkit-animation: fadeIn 5s;
    -moz-animation: fadeIn 5s;
    -o-animation: fadeIn 5s;
    -ms-animation: fadeIn 5s;
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@-webkit-keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}
.footer_text {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
}

.footer_text p {
    font-size: var(--paragraph);
    text-align: center;
    font-weight: 600;
}

.footer_text p span:nth-child(1) {
    color: var(--blue);
}

.footer_text p span:nth-child(2) {
    color: var(--orange);
}

.footer_text p span:nth-child(3) {
    color: var(--primary-red);
}
