        padding: 28px â€‹0px;
    }
}

@media only screen and (max-width: 1366px) and (min-width: 1026px) {
    .container {
        padding: 0px 91px;
        margin: 75px 0;
    }
    .welcome_text h1 {
        font-size: var(--large);
    }
    .name_text h1 {
        font-size: var(--xl);
    }
    .tagline {
        position: relative;
        /* left: 63px; */
    }
    .tagline h1 {
        font-size: var( --regular);
        text-align: left;
    }
    .avater_img img {
        width: 540px;
        height: 540px;
    }
    .about_containt {
        width: 528px;
        margin: 50px 0;
    }
    .about_containt p {
        font-size: 12px;
        line-height: 20px;
        text-align: justify;
    }
    .skills h1 {
        font-size: var(--medium);
        text-align: left;
    }
    .tech_logo img {
        width: 38px;
        height: 38px;
        margin: 0 3px;
    }
    .footer_text {
        position: relative;
        bottom: 0;
        left: 0;
        width: 100%;
    }
}

@media only screen and (max-width: 768px) and (min-width: 501px) {
    .header-container {
        display: flex;
        width: 100%;
        justify-content: center;
        align-items: flex-start;
    }
    .container {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        margin: 39px 0 0 0;
    }
    .main-heading {
        left: 0;
        text-align: center;
        margin: 27px 0;
    }
    .tagline h1 {
        text-align: center;
    }
    .skills h1 {
        text-align: center;
    }
    .skills>.tech_logo {
        justify-content: center;
    }
    .avater_img img {
        width: 288px;
        height: 288px;
    }
    .footer_text {
        position: relative;
    }
}

@media only screen and (max-width: 500px) and (min-width: 360px) {
    .header-container {
        display: flex;
        width: 100%;
        justify-content: center;
        align-items: flex-start;
    }
    .container {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        margin: 7px 0 0 0;
    }
    .main-heading {
        left: 0;
        text-align: center;
        margin: 27px 0;
    }
    .tagline h1 {
        text-align: center;
    }
    .skills h1 {
        text-align: center;
    }
    .skills>.tech_logo {
        justify-content: center;
    }
    .avater_img img {
        width: 288px;
        height: 288px;
    }
    .footer_text {
        position: relative;
    }
}

@media only screen and (max-width: 360px) and (min-width: 280px) {
    .container {
        margin: 10px 0;
    }
    .welcome_text h1 {
        font-size: 15px;
    }
    .name_text h1 {
        font-size: 38px;
    }
    .tagline h1 {
        font-size: 9px;
        text-align: center;
    }
    .about_containt {
        width: 100%;
    }
    .about_containt p {
        font-size: 9px;
    }
    .skills h1 {
        font-size: 15px;
    }
    .tech_logo img {
        width: 22px;
        height: 22px;
    }
    .avater_img img {
        width: 188px;
        height: 188px;
    }
    .footer_text p {
        font-size: 10px;
    }
}
:root {
    /* DEFINE COLORS CODES */
    --primary-red: #d82948;
    --violet: #460273;
    --blue: #0697f2;
    --orange: #f27d16;
    /* FONT SIZE */
    --small: 12px;
    --regular: 20px;
    --medium: 25px;
    --large: 48px;
    --xl: 61px;
    --paragraph: 16px
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body,
html {
    font-family: 'Nunito', sans-serif;
    font-weight: 400;
}
